define("cedarflo/templates/-welcome", ["exports"], function (exports) {
  exports["default"] = Ember.HTMLBars.template({ "id": "QV4ug8mP", "block": "{\"statements\":[[\"open-element\",\"h3\",[]],[\"flush-element\"],[\"text\",\"Dear friends and neighbors,\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"br\",[]],[\"flush-element\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"\\nWelcome to the final celebration of us as Cedarflo Angus. A small background\\nfor those who may be new. Cedarflo Angus has been raising registered angus for\\nover 70 years. We have been performance testing since 1968 and Dad (Dave)\\nstarted adding the finest genetics thru AI in 1979, allowing us to maintain a\\nclosed maternal herd. Traditionally holding a production sale in February,\\nselling both yearling bulls and replacement heifers, has allowed us to share\\nour genetics with local cattlemen who desire honest cattle. I’d like to think\\nthat through four generations we have been able to provide premium angus\\ngenetics with a real concern for both the animal and those purchasing them.\\nThank you to all of our customers. It has been a real joy working with you over\\nthe years, I know previous generations would agree.\\n\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"\\nAll animals in the sale have been through a comprehensive vaccination protocol,\\nprescribed by West River Vet Clinic. All bred animals have been ultrasounded by\\nWRVC in late summer to provide estimated calving dates, and will have pregnancy\\nconfirmed the week before the sale. This year’s heifers will be Bangs\\nvaccinated and will sell with registration papers if desired. Bull calves\\nselling, will be sold as is, unless the buyer choses to accept the terms of the\\n\"],[\"block\",[\"link-to\"],[\"sale.wintering\"],null,1],[\"text\",\".\\nYou can find the wintering program in subsequent pages.\\n\"],[\"open-element\",\"a\",[]],[\"static-attr\",\"href\",\"https://www.dvauction.com/video_catalogs/4065\"],[\"static-attr\",\"target\",\"_blank\"],[\"flush-element\"],[\"text\",\"DVAuction\"],[\"close-element\"],[\"text\",\"\\nwill be posting videos of all animals by the end of November. We will have any\\nfurther, pertinent information on our website: \"],[\"block\",[\"link-to\"],[\"index\"],null,0],[\"text\",\"\\nAnimals will be transported to Lemmon Livestock on Thursday, December 6th.\\nYou’re all welcome at the ranch anytime before then to inspect or just shoot\\nthe breeze.\\n\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"\\nThe decision to disperse was, by far, the hardest decision of my life. I’m\\nhoping to find time in my next life to do more than kiss my four-year-old,\\ngoodbye and goodnight. It’s impossible to adequately convey all the relevant\\ninformation about these animals in this short synopsis. These cattle are a\\nproduct of lifetimes of love and toil, hopefully you find them reflecting that\\nwell. Please contact me for further information. I promise to offer an honest\\nassessment of any of these cattle. We know them all and just like any other\\ncattlemen, we enjoy talking about our cows.\\n\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"We’ll miss you and thanks again,\"],[\"close-element\"],[\"text\",\"\\n\"],[\"open-element\",\"br\",[]],[\"flush-element\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Jeremy Fordahl\"],[\"close-element\"],[\"text\",\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"blocks\":[{\"statements\":[[\"text\",\"www.cedarfloangus.com.\"]],\"locals\":[]},{\"statements\":[[\"text\",\"wintering program\"]],\"locals\":[]}],\"hasPartials\":false}", "meta": { "moduleName": "cedarflo/templates/-welcome.hbs" } });
});